<style scoped>
  .container {
    background-color: #F5F5F5;
    padding: 0;
    margin: 0;
    margin-bottom: 54px;
  }

  #skin-container {
    width: 100%;
    margin-top: 44px;
  }

  .skin-test-intro {
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-end;
    padding: 12px;
    width: 100%;
    height: 160px;
    text-align: left;
    background-image: url(../img/skin_manage_bg.png);
    background-size: cover;
  }

  .skin-test-intro p:nth-child(1) {
    color: white;
    font-size: 16px;
  }

  .skin-test-intro p:nth-child(2) {
    margin-top: 12px;
    color: white;
    font-size: 12px;
  }

  /**/
  .skin-module {
    position: relative;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    padding: 12px;
    min-height: 300px;
    width: 100%;
    background-color: #F5F5F5;
  }

  .skin-module-item {
    margin-top: 12px;
    margin-left: 12px;
    padding: 19px 20px 20px 20px;
    width: calc(50% - 6px);
    list-style: none;
    background-color: white;
    box-shadow:0 0 4px 0 #dadada inset;
    text-align: center;
  }
  .skin-module-item img{
    background-color: #d0d0d0;
  }
  .skin-module li:nth-child(1), :nth-child(3) {
    margin-left: 0;
  }

  .skin-module li:nth-child(1), :nth-child(2) {
    margin-top: 0;
  }

  .skin-module-item p:nth-child(2) {
    margin-top: 3px;
    font-size: 14px;
    color: #323233;
    text-align: center;
  }

  .skin-module-item p:nth-child(3) {
    font-size: 12px;
    color: #323233;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-align: center;
  }

  .skin-module-item p:nth-child(4) {
    margin-top: 6px;
    font-size: 14px;
    color: #F8757E;
    text-align: center;
  }

  .check-skin {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 102px;
    height: 102px;
    border-radius: 50%;
    line-height: 102px;
    vertical-align: middle;
    background-color: #E1E1E1;
    border: 12px solid #F5F5F5;
    color: white;
    box-sizing: content-box;
  }
  .check-skin p{
    text-align: center;
  }
  /*  */
  .popup-container {
    display: none;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 910;
    width: 100vw;
    height: 100vh;
  }

  .popup-container::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    opacity: .7;
    background-color: #212121;
  }

  .bottom-popup {
    /*display: none;*/
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    background-color: white;
  }

  .bottom-popup p {
    margin-left: 12px;
    margin-right: 12px;
    padding: 12px 90px 12px 90px;
    color: #323233;
    font-size: 14px;
    text-align: center;
    border-bottom: 1px solid #E6E6E6;
  }

  .bottom-popup p:nth-child(1) {
    font-size: 12px;
  }

  .bottom-popup p:nth-child(2) {
    color: #F8757E;
  }
  .active {
    display: block;
  }
  .btnActive{
    background-color: #AC69FE;
  }
  /*  */
  .load-skin figure{
    margin:  0 auto;
    height: 48px;
    width: 48px;
    background-color: #e8e8e8;
  }
  .load-skin p{
    margin: 0 auto;
    width: 70px;
    height: 15px;
    background-color: #e8e8e8;
  }
  .load-skin p:nth-child(2){
    margin-top: 10px;
    width: 60px;
  }
  .load-skin p:nth-child(3){
    margin-top: 2px;
    width: 100px;
  }
</style>
<template>
  <div class="container">
    <topbar>
      <img slot="left" src="../img/hole_back.png" alt="" width="44" height="44" style="visibility: hidden">
      <p slot="title"></p>
      <img slot="right" src="../img/search.png" alt="" width="17" height="17" style="visibility: hidden">
    </topbar>
    <div id="skin-container">
      <!---->
      <section class="skin-test-intro">
        <p></p>
        <p>(Leslie<br>
          Baumann M.D.)The Skin Type Solution<br>
          
        </p>
      </section>
      <!---->
      <ul class="skin-module">
        <li v-show="loaded" class="skin-module-item" v-for="(item,index) in skinData" @click="popupSort(index)">
        <img :src="item.image" alt="" width="48" height="48" onerror="onerror=null;src='http://iph.href.lu/48x48'">
        <p>{{item.title}}</p>
        <p>{{item.desc}}</p>
        <p>{{skin[index]}}</p>
      </li>
        <li v-show="!loaded" class="skin-module-item load-skin" v-for="item in 4">
          <figure></figure>
          <p></p>
          <p></p>
          <p></p>
        </li>

        <!-- end-->
        <div class="check-skin" v-bind:class="{btnActive:isBtnActive}" @click="toSkinManage">
          <p></p>
        </div>
      </ul>
    </div>
    <!---->
    <div class="popup-container" :class="{ active: isActive }" @click="hide">
      <div class="bottom-popup">
        <p></p>
        <p></p>
        <p v-for="(item,index) in sortData" @click="selectSkin(index)">
          {{item.sortName}}
        </p>
      </div>
    </div>
  </div>
</template>
<script>
  import  {mapState, mapActions} from 'vuex'
  import topbar from '../components/topBar'
  var jsonp = require('jsonp');
  export default{
    data(){
      return {
        loaded: false,
        isActive: false,
        isBtnActive:false,
        skinData: [],
        sortData: [],
        skin:["","","",""],  //
        itemIndex:0,  //
      }
    },
    created(){
      this.fetchData();
    },
    components: {
      topbar
    },
    methods: {
      fetchData(){
        var that = this;
        let url = encodeURI('http://api.bevol.cn/init7');
        jsonp(url, null, function (err, data) {
          if (err) {
            console.error(err.message);
          } else {
            that.skinData = data.result.skinlists;
            that.loaded = true;
            that.$store.dispatch('initData', data.result);
            console.log(data);
          }
        });
      },
      popupSort(index){
        this.isActive = true;
        this.itemIndex = index;
        let that = this;
        switch (index) {
          case 0:
            this.sortData = [{sortName: ""}, {sortName: ""}, {sortName: ""}, {sortName: ""}];
            that.isActive = true;
            break;
          case 1:
            this.sortData = [{sortName: ""}, {sortName: ""}, {sortName: ""}, {sortName: ""}];
            that.isActive = true;
            break;
          case 2:
            this.sortData = [{sortName: ""}, {sortName: ""}];
            that.isActive = true;
            break;
          case 3:
            this.sortData = [{sortName: ""}, {sortName: ""}];
            that.isActive = true;
            break;
        }
      },
      hide(){
          this.isActive = false;
      },
      selectSkin(index){
        let that = this;
          switch (index){
            case 0 :
                that.skin[that.itemIndex] =  that.sortData[index].sortName;
               break;
            case 1 :
              that.skin[that.itemIndex] =  that.sortData[index].sortName;
              break;
            case 2 :
              that.skin[that.itemIndex] =  that.sortData[index].sortName;
              break;
            case 3 :
              that.skin[that.itemIndex] =  that.sortData[index].sortName;
              break;
            default:
                break;
          }
  // 4
       let isGoCount = 0;
        for(let i=0 ;i<4;i++){
              if(that.skin[i] == ""){
                  isGoCount += 1;
              }
        }
        if(isGoCount == 0 ){
            that.isBtnActive =true;
        }
  // end
      },
      toSkinManage(){
          if(this.isBtnActive){
              this.$router.push('skinManage');
          }else{
              return false;
          }
      }
    }
  }
</script>



